import React from 'react';
import Header from '../components/Header.jsx';
import Footer from '../components/Footer.jsx';

function Main({history, location}) {
    return (
        <>
            <Header history={history} location={location}></Header>
            <main>
                <section className="section0"></section>
                <section className="section1">
                <h2>사과</h2>
                <div className="poster">
                    <div className="option">
                    <ul>
                        <h3>옵션 1</h3>
                        <li>45과</li>
                        <li>4만원</li>
                    </ul>
                    </div>
                    <div className="option">
                    <ul>
                        <h3>옵션 2</h3>
                        <li>35과</li>
                        <li>5만원</li>
                    </ul>
                    </div>
                </div>
                </section>
                <section className="section2">
                <h2>선물용 사과</h2>
                <div className="poster">
                    <div className="option">
                    <ul>
                        <h3>옵션 1</h3>
                        <li>15 ~ 17과</li>
                        <li>4만원</li>
                    </ul>
                    </div>
                    <div className="option">
                    <ul>
                        <h3>옵션 2</h3>
                        <li>12 ~ 13과</li>
                        <li>5만원</li>
                    </ul>
                    </div>
                </div>
                </section>
                <section className="section3">
                <h2>사과즙</h2>
                <div className="poster">
                    <div>
                    <ul>
                        <h3>사과즙</h3>
                        <li>50포 ( 1포당 120ml )</li>
                        <li>3만원</li>
                        <li>*2박스 ( 5만원) </li>
                    </ul>
                    </div>
                </div>
                </section>
            </main>
            <Footer></Footer>
        </>
    );
}

export default Main;